export function createProduct(httpClient: HttpClient): CreateProductSignature {
  return (
    product: NonNullablePaths<
      V3Product,
      | `modifiers.${number}.choicesSettings.choices`
      | `name`
      | `options.${number}.choicesSettings.choices`
      | `physicalProperties.pricePerUnit.measurementUnit`
      | `productType`
      | `subscriptionDetails.subscriptions`
      | `subscriptionDetails.subscriptions.${number}.discount.type`
      | `subscriptionDetails.subscriptions.${number}.frequency`
      | `subscriptionDetails.subscriptions.${number}.title`
      | `variantsInfo`
      | `variantsInfo.variants`
      | `variantsInfo.variants.${number}.digitalProperties.digitalFile`
      | `variantsInfo.variants.${number}.digitalProperties.digitalFile._id`
      | `variantsInfo.variants.${number}.physicalProperties.pricePerUnit.settings.measurementUnit`
      | `variantsInfo.variants.${number}.price`
      | `variantsInfo.variants.${number}.price.actualPrice`
    >,
    options?: CreateProductOptions
  ) =>
    universalCreateProduct(
      product,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateProductSignature {
  /**
   * Creates a new product.
   *
   * This endpoint also allows to add a ribbon, brand, info sections, options, and modifiers.
   * @param - Product to create.
   *
   * At least 1 variant must be provided and each variant must have relevant item in `choices` field for every item in `options`.
   * If `options` is empty one default variant must be provided with empty `choices` list.
   * @returns Created product.
   */
  (
    product: V3Product,
    options?: CreateProductOptions | undefined
  ): Promise<V3Product>;
}

